import * as React from "react";
import { styled, alpha } from "@mui/material/styles";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import InputBase from "@mui/material/InputBase";
import "./App.css";
import SearchIcon from "@mui/icons-material/Search";
import Header from "./component/Header";
import { useState } from "react";
import axios from "axios";

const Search = styled("div")(({ theme }) => ({
  position: "relative",
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  "&:hover": {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: "100%",
  [theme.breakpoints.up("sm")]: {
    marginLeft: theme.spacing(1),
    width: "auto",
  },
}));

const SearchIconWrapper = styled("div")(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: "100%",
  position: "absolute",
  pointerEvents: "none",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: "inherit",
  width: "100%",
  "& .MuiInputBase-input": {
    padding: theme.spacing(1, 1, 1, 0),
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create("width"),
    [theme.breakpoints.up("sm")]: {
      width: "16ch",
      "&:focus": {
        width: "20ch",
      },
    },
  },
}));

export default function App() {
  const [timeoutId, updateTimeoutId] = useState();
  const [recipelist, setRecipelist] = useState([]);

  const fetchRecipe = async (searchString) => {
    const res = await axios.get(`https://api.edamam.com/api/recipes/v2?type=public&q=${searchString} &app_id=70fd1729&app_key=66aa5586341dcc907e7c1d1dacb39894&size=10`);
    setRecipelist(res.data.hits);
    console.log(res);
  };

  const texttrack = (event) => {
    clearTimeout(timeoutId);
    const newTimeout = setTimeout(() => fetchRecipe(event.target.value), 900);
    updateTimeoutId(newTimeout);
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar
        position="sticky"
        sx={{
          backgroundColor: "black",
          boxShadow:
            "rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px",
        }}
      >
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="open drawer"
            sx={{ mr: 2 }}
          >
            <img
              style={{
                width: "60px",
                height: "60px",
                display: "flex",
                borderRadius: "60px",
              }}
              src="https://cdn.worldvectorlogo.com/logos/food-2.svg"
              alt="Logo"
            />
          </IconButton>
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ flexGrow: 1, display: { xs: "none", sm: "block" } }}
          >
            Recipe Finder
          </Typography>
          <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Search Food"
              onChange={texttrack}
              inputProps={{ "aria-label": "search" }}
            />
          </Search>
        </Toolbar>
      </AppBar>

      <div className="main" style={{ display: "flex" }}>
        <div className="mainbox" style={{ display: "flex", flexWrap: "wrap", justifyContent: "center", alignItems: "center" }}>
          {recipelist.length > 0 ? (
            recipelist.map((recipeOBj) => (
              <Header key={recipeOBj.recipe.label} recipeOBj={recipeOBj} />
            ))
          ) : (
            <div style={{ display: "flex", justifyContent: "center", alignItems: "center",height:"80vh"}}>
              <img
                style={{ marginLeft:"590px",width:"100px",hieght:"100px", opacity:"0.7", padding: "10px", textAlign: "center" }}
                src="https://cdn.worldvectorlogo.com/logos/food-2.svg"
                alt="Logo"
              />
            </div>
          )}
        </div>
      </div>
    </Box>
  );
}
