import React from 'react';
import "./Header.css";
import { useState } from 'react';
import Dialog from '@mui/material/Dialog';
// import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
// import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

function Header(props) {
  const { recipeOBj } = props;
  const { recipe } = recipeOBj;


const[show, setShow]=useState(false);
const handleClose = () => {
  setShow(false);
};

  return (
    <div>
   <Dialog open={show} onClose={handleClose} >

      <DialogTitle id="alert-dialog-slide-title"> Ingredients</DialogTitle>
      <DialogContent>
        <table>
          <thead>
            <th> Ingredients </th>
            <th> Weight </th>

          </thead>

          <tbody>
  {recipe.ingredients.map((ingredientsOBJ) => (
    <tr key={ingredientsOBJ.text}>
      <td>{ingredientsOBJ.text}</td>
      <td>{ingredientsOBJ.weight}</td>
    </tr>
  ))}
</tbody>
<button onClick={handleClose} > close </button>
        </table>
      </DialogContent>
    </Dialog>
      <div className="recipeBoxContainer">
        <div className="recipeBox">
          <img src={recipe.image}  alt='error'/>
          <div className="recipeDescription">
            <span>{recipe.label}</span>
            <span  onClick={()=>setShow(true)} >Ingredients</span>
            <span onClick={()=> window.open(recipeOBj.url)}>See complete Recipe</span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Header;
